.work-item {

 &__video {
 position: relative;
 width: 100%;
 height: 0;
 padding-top: 51.2%;
 opacity: .68;
 transition: opacity .4s ease-out;
 margin-bottom: 1em;
 overflow: hidden;

 video {
 position: absolute;
 top: 0;
 left: 0;
 width: 100%;
 min-height: 100%;
 }

 &.in-view {
 opacity: 1;
 }
 }
}